import { makeAutoObservable } from 'mobx';

import { IS_MOCK, MOCK } from './mock';

interface Author {
  role: 'system' | 'user' | 'assistant';
  metadata?: Record<string, any>;
}

interface Content {
  content_type: 'text';
  parts: string[];
}

export interface Message {
  id: string;
  author: Author;
  create_time: number;
  content: Content;
  weight: number;
  metadata?: Record<string, any>;
  recipient: 'all';
}

interface MappingItem {
  id: string;
  message?: Message;
  parent?: string;
  children?: string[];
}

interface DialogData {
  title: string;
  create_time: number;
  update_time: number;
  mapping: Record<string, MappingItem>;
  moderation_results: any[];
  current_node: string;
}

function isDialogData(data: any): data is DialogData {
  return (
    typeof data.title === 'string' &&
    typeof data.create_time === 'number' &&
    typeof data.update_time === 'number' &&
    typeof data.mapping === 'object' &&
    Array.isArray(data.moderation_results) &&
    typeof data.current_node === 'string'
  );
}

class DialogStore {
  data: DialogData | null = null;

  constructor() {
    makeAutoObservable(this);
  }

  setData(data: DialogData) {
    this.data = data;
  }

  get messages() {
    if (!this.data) return [];
    return Object.values(this.data.mapping).map((item: any) => item.message);
  }

  async fetchData() {
    try {
      if (IS_MOCK) {
        const mock = MOCK;

        if (isDialogData(mock)) {
          this.setData(mock);
        } else {
          console.error('Invalid data format:', mock);
        }
      } else {
        const response = await fetch('/api/mock');
        const data = await response.json();

        if (isDialogData(data)) {
          this.setData(data);
        } else {
          console.error('Invalid data format:', data);
        }
      }
    } catch (error) {
      console.error('Failed to fetch data:', error);
    }
  }
}

export const dialogStore = new DialogStore();
