/* eslint-disable import/no-unresolved */
import React from 'react';
import { observer } from 'mobx-react-lite';
import { Light as SyntaxHighlighter } from 'react-syntax-highlighter';
import js from 'react-syntax-highlighter/dist/esm/languages/hljs/javascript';
import ts from 'react-syntax-highlighter/dist/esm/languages/hljs/typescript';
import { monokaiSublime } from 'react-syntax-highlighter/dist/cjs/styles/hljs';

import {dialogStore, Message} from './store';
import { useStyles } from './useStyles';

SyntaxHighlighter.registerLanguage('javascript', js);
SyntaxHighlighter.registerLanguage('typescript', ts);

function parseText(
  text: string,
): Array<string | { language: string; code: string }> {
  const regex = /```(\w+)\n([\s\S]+?)```/g;
  const fragments: Array<string | { language: string; code: string }> = [];
  let lastIndex = 0;
  let match;

  while ((match = regex.exec(text)) !== null) {
    fragments.push(text.slice(lastIndex, match.index));
    fragments.push({ language: match[1], code: match[2] });
    lastIndex = regex.lastIndex;
  }

  if (lastIndex < text.length) {
    fragments.push(text.slice(lastIndex));
  }

  return fragments;
}

const Dialog = observer(() => {
  const { data } = dialogStore;
  const classes = useStyles();

  React.useEffect(() => {
    dialogStore.fetchData();
  }, []);

  if (!data) return <div>Loading...</div>;

  return (
    <div className={classes.dialog}>
      {dialogStore.messages.map((message: Message, index: number) => (
        <div
          key={index}
          className={`${classes.message} ${classes[message.author.role]}`}
        >
          <div className={classes.author}>{message?.author?.role}:</div>
          <div className={classes.content}>
            {parseText(message?.content?.parts?.join('') || '').map(
              (fragment, i) => {
                if (typeof fragment === 'string') {
                  return <span key={i}>{fragment}</span>;
                } else {
                  return (
                    <SyntaxHighlighter
                      key={i}
                      language={fragment.language}
                      style={monokaiSublime}
                    >
                      {fragment.code}
                    </SyntaxHighlighter>
                  );
                }
              },
            )}
          </div>
        </div>
      ))}
    </div>
  );
});

export default Dialog;
