            "src/index.tsx:\n\n```tsx\nimport React from 'react';\nimport ReactDOM from 'react-dom';\nimport App from './App';\n\nReactDOM.render(<App />, document.getElementById('root'));\n```\n\nsrc/App.tsx:\n\n```tsx\nimport React from 'react';\nimport Dialog from './Dialog';\n\nfunction App() {\n  return <Dialog />;\n}\n\nexport default App;\n```\n\nsrc/Dialog.tsx:\n\n```tsx\nimport React, { useEffect } from 'react';\nimport { observer } from 'mobx-react-lite';\nimport { Light as SyntaxHighlighter } from 'react-syntax-highlighter';\nimport { docco } from 'react-syntax-highlighter/dist/esm/styles/hljs';\nimport { dialogStore } from './store';\n\nconst Dialog = observer(() => {\n  useEffect(() => {\n    dialogStore.fetchData();\n  }, []);\n\n  if (!dialogStore.data) return <div>Loading...</div>;\n\n  const messages = Object.values(dialogStore.data.mapping).map((item: any) => item.message);\n\n  return (\n    <div>\n      {messages.map((message: any, index: number) => (\n        <div key={index}>\n          <p>{message.author.role}: </p>\n          <SyntaxHighlighter language=\"typescript\" style={docco}>\n            {message.content.parts.join('')}\n          </SyntaxHighlighter>\n        </div>\n      ))}\n    </div>\n  );\n});\n\nexport default Dialog;\n```\n\nsrc/store.ts:\n\n```tsx\nimport { makeAutoObservable } from 'mobx';\n\ninterface Author {\n  role: 'system' | 'user' | 'assistant';\n  metadata?: Record<string, any>;\n}\n\ninterface Content {\n  content_type: 'text';\n  parts: string[];\n}\n\ninterface Message {\n  id: string;\n  author: Author;\n  create_time: number;\n  content: Content;\n  weight: number;\n  metadata?: Record<string, any>;\n  recipient: 'all';\n}\n\ninterface MappingItem {\n  id: string;\n  message?: Message;\n  parent?: string;\n  children?: string[];\n}\n\ninterface MockData {\n  title: string;\n  create_time: number;\n  update_time: number;\n  mapping: Record<string, MappingItem>;\n  moderation_results: any[];\n  current_node: string;\n}\n\nfunction isMockData(data: any): data is MockData {\n  return (\n    typeof data.title === 'string' &&\n    typeof data.create_time === 'number' &&\n    typeof data.update_time === 'number' &&\n    typeof data.mapping === 'object' &&\n    Array.isArray(data.moderation_results) &&\n    typeof data.current_node === 'string'\n  );\n}\n\nclass DialogStore {\n  data: MockData | null = null;\n\n  constructor() {\n    makeAutoObservable(this);\n  }\n\n  setData(data: MockData) {\n    this.data = data;\n  }\n\n  async fetchData() {\n    try {\n      const response = await fetch('/api/mock');\n      const data = await response.json();\n\n      if (isMockData(data)) {\n        this.setData(data);\n      } else {\n        console.error('Invalid data format:', data);\n      }\n    } catch (error) {\n      console.error('Failed to fetch data:', error);\n    }\n  }\n}\n\nexport const dialogStore = new DialogStore();\n```",
